<template>
  <div>
    <Panel title="">
      <h1></h1>
      <p>
        <el-button type="primary" @click.native="onAlert">($alert)</el-button>
        <el-button type="primary" @click.native="onAlert2">icon($alert)</el-button>
      </p>
      <p>
        <el-button @click.native="onConfirm">($confirm)</el-button>
      </p>
      <h1></h1>
      <p>
        <el-button type="primary" @click.native="onMessage">($message)</el-button>
        <el-button type="primary" @click.native="onMessage2">($message)</el-button>
        <el-button type="primary" @click.native="onMessage3">($message)</el-button>
      </p>
    </Panel>
    <Panel title="">
      <el-button @click.native="dialogVisible = true"></el-button>
      <el-button @click.native="dialogVisible2 = true">2</el-button>
    </Panel>
    <el-dialog title="" v-model="dialogVisible">
      <el-form ref="form" :model="form" label-width="5em">
        <el-form-item label="">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-checkbox-group v-model="form.type">
            <el-checkbox label="/" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.resource">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="form.desc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button class="fl"></el-button>
        <el-button @click.native="dialogVisible = false"> </el-button>
        <el-button type="primary" @click.native="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" v-model="dialogVisible2">
    
      <Divider></Divider>

      <LabelGrid v-model="radio">
        <el-radio label="1"></el-radio>
        <el-radio label="2"></el-radio>
        <el-radio label="3">
          <el-tooltip content="" placement="top">
            <span></span>
          </el-tooltip>
        </el-radio>
        <el-radio label="4"></el-radio>
        <el-radio label="5">
          <el-tooltip content="" placement="top">
            <span></span>
          </el-tooltip>
        </el-radio>
        <el-radio label="6">
          <el-tooltip content="" placement="top">
            <span></span>
          </el-tooltip>
        </el-radio>
      </LabelGrid>

      <Divider></Divider>

      <LabelGrid v-model="checkList">
        <el-checkbox label=" A">A</el-checkbox>
        <el-checkbox label=" B">
          <el-tooltip content="" placement="top">
            <span></span>
          </el-tooltip>
        </el-checkbox>
        <el-checkbox label=" C">c</el-checkbox>
        <el-checkbox label=" D">d</el-checkbox>
        <el-checkbox label=" E">e</el-checkbox>
      </LabelGrid>

      <span slot="footer" class="dialog-footer">
        <el-button @click.native="dialogVisible2 = false"> </el-button>
        <el-button type="primary" @click.native="dialogVisible2 = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<style lang="less">
  @import '~assets/styles/var.less';
  
</style>
<script>
export default {
  data() {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      radio: '1',
      checkList: [],
      dialogVisible: false,
      dialogVisible2: false
    }
  },
  methods: {
    onAlert() {
      this.$alert('', '')
      .then(() => {
        console.log('...')
      })
    },
    onAlert2() {
      this.$alert('', '', {
        type: 'success'
        // type: 'error'
        // type: 'info'
        // type: 'warning'
      })
    },
    onConfirm() {
      this.$confirm(', ?', '', {
        // confirmButtonText: '',
        // cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        console.log('...')
      }).catch(() => {
        console.log('...')
      })
    },
    onMessage() {
      this.$message('~')
      // this.$message.success('~')
      // this.$message.error('~')
      // this.$message.warning('~')
    },
    onMessage2() {
      this.$message({
        type: 'success',
        message: '!',
        duration: 0,
        showClose: true
      })
    },
    onMessage3() {
      this.$message({
        type: 'success',
        message: '!',
        showClose: true
      })
    }
  }
}
</script>



